			small 
			class="edit_btn" 
			absolute
			to="/lk/profile-settings" 
		>
			<v-icon color="white">mdi-lead-pencil</v-icon>
		</v-btn>

		<v-row no-gutters>
			<v-col align="center">
				<v-img v-if="userData.role == 'COACH'"  width="110" src="@/assets/coach_avatar.png"/>
