<script>
import {mapGetters} from 'vuex'

export default {
	computed:{
    ...mapGetters(['myEntries', 'adsList', 'visitsList', 'userData']),
  },
	methods: {
		logout() {
			this.$store.dispatch('fetchJsonData', '/auth/logout')
		}
	}
}
</script>

<style>
.edit_btn {
	left: 74%;
}
</style>