<template>
	<div style="height: 100%; width: 100%">
		<template v-if="isLoading">
			<div class="center-outer" style="height: 100%; width: 100%">
				<v-progress-circular
					:size="100"
					:width="8"
					color="primary"
					indeterminate
					class="center-inner"
				/>
			</div>
		</template>
		<template v-else>
			<component :is="layout"/>
		</template>
	</div>
</template>

<script>
import BaseLayout from '@/views/layouts/BaseLayout'
import LoginLayout from '@/views/layouts/LoginLayout'

export default {
	data: () => ({
		isLoading: true,
	}),

  components: { BaseLayout, LoginLayout },

  async beforeCreate() {
		this.isLoading = true
    this.$store.dispatch('loadToken')
		await this.$store.dispatch('fetchJsonData', '/auth/check-login')
		this.isLoading = false
  },

  computed: {
		layout() {
			if (this.$store.getters.isLogin) {
				return 'BaseLayout'
			}
			else {
				return 'LoginLayout'
			}
		}
	},
}
</script>

<style>
.center-outer:before {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.center-outer {
	text-align: center;
}

.center-inner {
  display: inline-block;
  vertical-align: middle;
}
</style>