<template>
  <v-stepper v-model="registrationStep">
		<v-stepper-items>


			<v-stepper-content step="1">
				<v-form @submit.prevent="registrationStep1" v-model="validStep1">
					<v-row>
						<v-col align="center">
							<v-text-field 
								label="Студенческий билет" 
								required 
								class="mt-4"
								:rules="[ requiredFieldRule, studCodeFieldRule ]"
								v-model="studCode"
							/>
						</v-col>
					</v-row>
					<v-row no-gutters>
						<v-col align="center">
							<v-btn 
								color="primary"  
								type="submit"
								class="mt-5 white--text"
								:disabled="!validStep1"
								:loading="isLoading"
							>
								Далее
							</v-btn>
						</v-col>
					</v-row>
				</v-form>
			</v-stepper-content>


			<v-stepper-content step="2">
				<v-form @submit.prevent="registrationStep2" v-model="validStep2">
					<v-row no-gutters>
						<v-col align="center">
							<v-text-field 
								label="Придумайте пароль" 
								type="password" 
								required
								:rules="[ requiredFieldRule, passwordFieldRule ]"
								v-model="password"
							/>
						</v-col>
					</v-row>
					<v-row no-gutters>
						<v-col align="center">
							<v-text-field 
								label="Подтвердите пароль" 
								type="password" 
								required
								:rules="[ 
									requiredFieldRule,
									() => password == repeatPassword || 'Пароли не совпадают'
								]"
								v-model="repeatPassword"
							/>
						</v-col>
					</v-row>
					<v-row no-gutters>
						<v-col align="center">
							<v-btn 
								color="primary" 
								@click="(registrationStep = 1)"
								class="mt-5 white--text"
							>
								Назад
							</v-btn>	
							<v-btn 
								color="primary"
								type="submit"
								class="mt-5 ml-3 white--text"
								:disabled="!validStep2"
								:loading="isLoading"
							>
								Далее
							</v-btn>
						</v-col>
					</v-row>
				</v-form>
			</v-stepper-content>


			<v-stepper-content step="3">
				<v-form @submit.prevent="registrationStep3" v-model="validStep3">
					<v-row no-gutters>
						<v-col align="center">
							<v-text-field 
								label="Фамилия"
								required
								:rules="[ requiredFieldRule ]"
								v-model="firstname"
							/>
						</v-col>
					</v-row>
					<v-row no-gutters>
						<v-col align="center">
							<v-text-field 
								label="Имя"
								required
								:rules="[ requiredFieldRule ]"
								v-model="secondname"
							/>
						</v-col>
					</v-row>
					<v-row no-gutters>
						<v-col align="center">
							<v-text-field 
								label="Отчество"
								required
								:rules="[ requiredFieldRule ]"
								v-model="surname"
							/>
						</v-col>
					</v-row>
					<v-row no-gutters>
						<v-col align="center">
							<GroupSelectorBtn 
								:selected_group="selectedGroup"
								@changeGroup="changeGroup"
							/>
						</v-col>
					</v-row>
					<v-row no-gutters>
						<v-col align="center">
							<v-btn 
								color="primary"
								@click="(registrationStep = 2)"
								class="mt-5 white--text"
							>
								Назад
							</v-btn>

							<v-btn 
								color="primary"
								type="submit"
								class="mt-5 ml-3 white--text"
								:disabled="!validStep3 || selectedGroup == 'Выбрать группу'"
								:loading="isLoading"
							>
								Далее
							</v-btn>
						</v-col>
					</v-row>
				</v-form>
			</v-stepper-content>
		</v-stepper-items>
	</v-stepper>
</template>

<script>
import GroupSelectorBtn from '@/components/ui/GroupSelectorBtn'

export default {
	data: () => ({
		isLoading: false,

		validStep1: false,
		validStep2: false,
		validStep3: false,

		registrationStep: 1,

		studCode: '',
		password: '',
		repeatPassword: '',
		firstname: '',
		secondname: '',
		surname: '',
		selectedGroup: 'Выбрать группу',

		requiredFieldRule: v => !!v || 'Поле обязательно',
		studCodeFieldRule: v => v.length == 8 || 'Длина студенческого билета 8 символов',
		passwordFieldRule: v => ( v.length >= 8 && v.length <= 64 ) || 'Допустимая длина от 8 до 64 символов',
	}),
	methods: {
		registrationStep1() {
			// this.isLoading = true
			// this.$store.dispatch('checkStudCode', this.studCode)
			// this.isLoading = false
			this.registrationStep = 2
		},
		registrationStep2() {

			this.registrationStep = 3
		},
		registrationStep3() {
			this.isLoading = true
			const userData = {
				'student_card': this.studCode,
				'firstname': this.firstname,
				'secondname': this.secondname,
				'surname': this.surname,
				'group': this.selectedGroup,
				'password': this.password,
				'token': true,
			}
			this.$store.dispatch('pushJsonData', { endpoint: '/auth/signup', body: userData })

			this.isLoading = false
		},
		changeGroup(new_group) {
			this.selectedGroup = new_group
		}
	},
	components: { GroupSelectorBtn }
}
</script>

