<template>
  <v-form @submit.prevent="tryLogin" v-model="valid">
		<v-card>
			<v-card-text>
				<v-row>
					<v-col align="center">
						<v-text-field 
							label="Студенческий билет" 
							class="mt-3"
							required
							:rules="[ requiredFieldRule ]"
							v-model="studCard"
						/>
					</v-col>
				</v-row>
				<v-row  no-gutters>
					<v-col align="center">
						<v-text-field 
							type="password" 
							label="Пароль" 
							required
							:rules="[ requiredFieldRule, passwordFieldRule ]"
							v-model="password"
						/>
					</v-col>
				</v-row>

				<v-row no-gutters>
					<v-col align="center">
						<v-btn 
							color="primary" 
							type="submit"
							class="mt-5 white--text"
							:disabled="!valid"
							:loading="isLoading"
						>
							Войти
						</v-btn>
					</v-col>
				</v-row>
			</v-card-text>
		</v-card>
		
	</v-form>
</template>

<script>
export default {
	data: () => ({
		valid: false,
		isLoading: false,

		studCard: '',
		password: '',

		requiredFieldRule: v => !!v || 'Поле обязательно',
		passwordFieldRule: v => ( v.length >= 8 && v.length <= 64 ) || 'Допустимая длина от 8 до 64 символов',
	}),

	methods: {
		tryLogin() {
			this.isLoading = true
			const loginData = { 
				'student_card': this.studCard,
				'password': this.password,
				'token': true,
			}
			this.$store.dispatch('pushJsonData', { endpoint: '/auth/login', body: loginData })
			this.isLoading = false
		},
	}
}
</script>

